import React from 'react'
import * as FaIcon from 'react-icons/fa'
import * as BsIcon from 'react-icons/bs'
import * as SiIcon from 'react-icons/si'
import * as AiIcon from 'react-icons/ai'
const FStyle = ()=>{
    return (
        <style>
            {`
                 @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
                 .font-fam{
                     font-family: 'Poppins', sans-serif;
                 }
                 .md{
                     flex-wrap:wrap;
                     flex-direction:column;
                 }
                 .nav-item-f{
                     list-style: none;
                 }
                 .two-button{
                     border: 3px solid black;
                     background-color: white;
                     color: black;
                     padding: 8px 15px;
                     font-size: 18px;
                     font-weight: bold;
                     width:150px;
                     margin:auto;
                 }
                 .two-button:hover{
                     background-color: black;
                     color: white;
                 }
            `}
        </style>
    )
}

const Footer = () => {
  return (
    <div>
        <div class="container-fluid bg-dark text-white py-5">
            <div class="row">
                <div class="col-12 my-4 col-md-3 m-auto text-center">
                <img src="https://de.cdn-website.com/17b2a37b2d0b4ccd81c1e97900ae3e66/dms3rep/multi/frsh_food_logo-02-207x113.svg" alt="" style={{height:"80px", width:"190px",backgroundColor:"white",borderRadius:"14px"}} />
                </div>
                <div class="col-12 my-4 col-md-3 m-auto text-center font-fam">
                    <p>10 Street Name</p>
                    <p>Country, Zip code</p>
                    <p>City Name</p>
                </div>
                <div class="col-12 my-4 col-md-3 m-auto text-center font-fam">
                    <p>Contact Us</p>
                    <p>+(00) 55 5555 5555</p>
                    <p>example@mail.com</p>
                </div>
                <div class="col-12 my-4 col-md-3 m-auto d-flex justify-content-center md">
                    <div className=' m-auto d-flex justify-content-center mb-3'>
                    <li className="nav-item-f ms-5 me-2">
                    <a className="nav-link text-white" href="/" tabIndex="-1" aria-disabled="true"><FaIcon.FaFacebookF/></a>
                    </li>
                    <li className="nav-item-f me-2">
                    <a className="nav-link text-white" href="/" tabIndex="-1" aria-disabled="true"><BsIcon.BsTwitter/></a>
                    </li>
                    <li className="nav-item-f me-2">
                    <a className="nav-link text-white" href="/" tabIndex="-1" aria-disabled="true"><SiIcon.SiTripadvisor/></a>
                    </li>
                    <li className="nav-item-f me-2">
                    <a className="nav-link text-dark text-white" href="/" tabIndex="-1" aria-disabled="true"><AiIcon.AiOutlineInstagram/></a>
                    </li>
                    </div>
                    <div className='text-center'><button className='two-button ms-3 font-fam'>Read More</button></div>
                </div>
            </div>
        </div>
        <FStyle/>
    </div>
  )
}

export default Footer