import React from 'react'
import * as FaIcon from 'react-icons/fa'
import * as BsIcon from 'react-icons/bs'
import * as SiIcon from 'react-icons/si'
import * as AiIcon from 'react-icons/ai'
const NaStyle = ()=>{
  return (
    <style>
      {`
        @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
        .font-fam{
            font-family: 'Poppins', sans-serif;
        }
      `}
    </style>
  )
}

const Navbar = () => {
  return (
    <div>
          <nav className="navbar navbar-expand-lg navbar-light bg-light fixed-top" style={{color:"black"}}>
            <div className="container d-flex justify-content-between">
                <a className="navbar-brand ms-5" href="/"><img src="https://de.cdn-website.com/17b2a37b2d0b4ccd81c1e97900ae3e66/dms3rep/multi/frsh_food_logo-02-207x113.svg" alt="jpg" style={{height:"80px", width:"190px"}} /></a>
               <div className='second-m'>
               <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav me-5  mb-2 mb-lg-0 font-fam">
                    <li className="nav-item me-4">
                    <a className="nav-link text-dark" aria-current="page" href="/">Home</a>
                    </li>
                    <li className="nav-item me-4">
                    <a className="nav-link text-dark" href="/" tabIndex="-1" aria-disabled="true">About</a>
                    </li>
                    <li className="nav-item me-4">
                    <a className="nav-link text-dark" href="/" tabIndex="-1" aria-disabled="true">Menu</a>
                    </li>
                    <li className="nav-item me-5">
                    <a className="nav-link text-dark" href="/" tabIndex="-1" aria-disabled="true">Gallary</a>
                    </li>
                    <li className="nav-item me-2">
                    <a className="nav-link text-dark" href="/" tabIndex="-1" aria-disabled="true"><FaIcon.FaFacebookF/></a>
                    </li>
                    <li className="nav-item me-2">
                    <a className="nav-link" href="/" tabIndex="-1" aria-disabled="true"><BsIcon.BsTwitter/></a>
                    </li>
                    <li className="nav-item me-2">
                    <a className="nav-link" href="/" tabIndex="-1" aria-disabled="true"><SiIcon.SiTripadvisor/></a>
                    </li>
                    <li className="nav-item me-2">
                    <a className="nav-link text-dark" href="/" tabIndex="-1" aria-disabled="true"><AiIcon.AiOutlineInstagram/></a>
                    </li>
                </ul>
                </div>
               </div>
            </div>
            </nav>
            <NaStyle/>
    </div>
  )
}

export default Navbar