import React from 'react'
const OpenStyle = ()=>{
    return (
        <style>
            {`
            @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
            .font-fam{
                font-family: 'Poppins', sans-serif;
            }
                .item-pic a img{
                    width: 100%;
                    height: 350px;
                    object-fit: cover;
                }
                .item-pic a{
                    position: relative;
                    color:white;
                }
                .item-pic a:hover{
                    background: rgba(167, 163, 163, 0.555);
                }
                .item-pic a h5{
                    position: absolute;
                    top: 48%;
                    left: 32%;
                    font-size:25px;
                }  

            `}
        </style>
    )
}
const OpenH = () => {
  return (
    <div className='bg-light py-5 font-fam'>
        <div className="menu text-center mb-5">    
            <h2>Menu</h2>
        </div>
        <div className="menu-items">
        <div class="container-fluid">
        <div class="row">
            <div class="col-12 my-3 col-md-4 px-5 m-auto item-pic">
            <a className='text-decoration-none' href="/">
                <img src="https://img.theculturetrip.com/450x/smart/wp-content/uploads/2018/03/1943066.jpg" alt="" />
                <h5>lunch</h5>
            </a>
            </div>
            <div class="col-12 my-3 col-md-4 px-5 m-auto item-pic font-fam">
                <div className='d-flex justify-content-between'><p>Monday</p> <p>Closed</p></div>
                <div className='d-flex justify-content-between'><p>Tue - Thu</p> <p>11:00am - 10:00 pm</p></div>
                <div className='d-flex justify-content-between'><p>Friday</p> <p>11:00 am - 11:00 pm</p></div>
                <div className='d-flex justify-content-between'><p>Saturday</p> <p>5:00 pm - 11:00 pm</p></div>
                <div className='d-flex justify-content-between'><p>Sunday</p> <p>11:00 am - 9:00 pm</p></div>
            </div>
            <div class="col-12 my-3 col-md-4 px-5 m-auto item-pic">
            <a className='text-decoration-none font-fam' href="/">
                <img src="https://risanb.com/code/colorful-google-maps-marker/default-marker.jpg" alt="" />
                <h5>breakfast</h5>
            </a>
            </div>
        </div>
        </div>
        </div>

    <OpenStyle/>
    </div>
  )
}

export default OpenH