import React from 'react'
import OpenH from './OpenH'

import Sectiona from './Sectiona'

const OneStyle = ()=>{
    return (
        <style>
            {`
            @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
            .font-fam{
                font-family: 'Poppins', sans-serif;
            }
                .section-one{
                    padding-top:80px;
                    z-index: 100;
                    width: 100%;
                    height: 100vh;
                    display:flex;
                    justify-content:center;
                    align-items:center;
                    flex-direction: column;
                }
                .section-one p{
                    font-size: 18px;
                    color:white;
                }
                .section-one h4{
                    font-size: 80px;
                    color:white;
                    width:650px;
                    margin:30px 0;
                }
                .one-button{
                    border: 3px solid white;
                    background-color: transparent;
                    color: white;
                    padding: 8px 25px;
                    font-size: 18px;
                    font-weight: bold;
                }
                .one-button:hover{
                    background-color: white;
                    color: black;
                    
                }
                .two-button{
                    border: 3px solid black;
                    background-color: white;
                    color: black;
                    padding: 8px 25px;
                    font-size: 18px;
                    font-weight: bold;
                }
                .two-button:hover{
                    background-color: black;
                    color: white;
                }

                .first-section{
                    display: flex;
                    justify-content: center;
                    padding: 20px;
                    align-items: center;
                    flex-wrap: wrap;
                    background-color: white;
                    color: black;
                }
                .first-section .left-fs{
                    width: 460px;
                    padding:30px;
                }
                .first-section .right-fs{
                    width: 460px;
                }
                
                .first-section .right-fs img{
                    width: 300px;
                    height: 300px;
                    border-radius: 50%;
                    margin: 20px 20px 20px 130px;
                }
                @media screen and (max-width:720px){
                    .section-one{
                        width: 360px;
                        // font-size: 30px;
                        color:white;
                        margin:auto;
                    }
                    .section-one h4{
                        width: 360px;
                        font-size: 30px;
                        color:white;
                        margin:30px 0;
                    }
                    .section-one p{
                        font-size: 15px;
                        color:white;
                    }
                    .first-section .right-fs{
                        width: 360px;
                    }
                    
                    .first-section .right-fs img{
                        width: 300px;
                        height: 300px;
                        border-radius: 50%;
                        margin: 20px 20px 20px 20px;
                    }
                    
                }
                
            `}
        </style>
    )
}

const SectionOne = () => {
  return (
    <div>
        <div className='section-one text-center font-fam'>
        <p className='w-pera'>WELCOME TO FRESH FOOD LOCAL GOURMET</p>
        <h4>Serving delicious local food</h4>
        <button className='one-button'>Find a Table</button>
        </div>

        <div className="first-section font-fam">
            <div className='left-fs font-fam'>
                    <h3 className='mb-5'>Our story</h3>
                    <p className='mb-5'>Here at Local Gourmet, our daily goal is to create, serve and enjoy fine food. We love fresh food, flavorful food, food that’s in season, and food that’s distinct. Every morning before opening the Local Gourmet kitchen, we stock our shelves with market fresh ingredients, so that the food we serve you is as fresh and flavorful as can be. We welcome you to our restaurant, and wish you a hearty appetite. </p>
                    <button className='two-button'>Read More</button>
            </div>
            <div className='right-fs'>
                <img src="https://le-de.cdn-website.com/17b2a37b2d0b4ccd81c1e97900ae3e66/dms3rep/multi/opt/670b480ecbec4647a6dde11ab5a0a1f7-723x723.dm.edit_kgNy5F-1920w.jpg" alt="pic" />
            </div>
        </div>
         <Sectiona/>
       <OpenH/>

    <OneStyle/>
    </div>
  )
}

export default SectionOne